import React from "react";
import CourseCard from "app/views/common/components/CourseCard";
import Button from "react-bootstrap/Button";
import Tabs from "react-bootstrap/Tabs";
import Tab from "react-bootstrap/Tab";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { Redirect } from "react-router-dom";
import "./courses.scss";
import BitCard from "app/views/common/components/BitCard";

function CoursesComponent(props) {
  const {
    courses: {
      isLoading: coursesIsLoading,
      error: coursesError,
      errMsg: coursesErrMsg,
      data: coursesData,
    },
    bits: {
      isLoading: bitsIsLoading,
      error: bitsError,
      errMsg: bitsErrMsg,
      data: bitsData,
    },
    openBitDetails,
    openCourseDetails,
    goToAddCourse,
    goToAddBit,
    toAddCourse,
    toAddBit,
    activeSection,
    switchView,
  } = props;

  if (toAddCourse) return <Redirect push to={{ pathname: "/add-course" }} />;
  else if (toAddBit) return <Redirect push to={{ pathname: "/add-bit" }} />;
  else
    return (
      <div id="cfg_courses_page" className="page">
        <Tabs
          id="controlled-tab-example"
          activeKey={activeSection}
          onSelect={switchView}
        >
          <Tab eventKey="courses" title={<b>Courses</b>}>
            {coursesIsLoading ? (
              <>Loading courses...</>
            ) : coursesError ? (
              <>{coursesErrMsg}</>
            ) : (
              <>
                <div className="text-right mt-3">
                  <Button
                    variant="success"
                    className="ml-2"
                    onClick={goToAddCourse}
                  >
                    <FontAwesomeIcon icon={faPlus} className="mr-2" />
                    Add Course
                  </Button>
                </div>
                {coursesData.map((courseInfo) => (
                  <div key={`${courseInfo.id}`}>
                    <CourseCard
                      {...courseInfo}
                      openCourseDetails={openCourseDetails}
                    />
                    <div className="course-seperator"></div>
                  </div>
                ))}
              </>
            )}
          </Tab>
          <Tab eventKey="bits" title={<b>Bits</b>}>
            {bitsIsLoading ? (
              <>Loading bits...</>
            ) : bitsError ? (
              <>{bitsErrMsg}</>
            ) : (
              <>
                <div className="text-right mt-3">
                  <Button
                    variant="success"
                    className="ml-2"
                    onClick={goToAddBit}
                  >
                    <FontAwesomeIcon icon={faPlus} className="mr-2" />
                    Add Bit
                  </Button>
                </div>
                {bitsData.map((bitInfo) => (
                  <div key={`${bitInfo.id}`}>
                    <BitCard {...bitInfo} openBitDetails={openBitDetails} />
                    <div className="course-seperator"></div>
                  </div>
                ))}
              </>
            )}
          </Tab>
        </Tabs>
      </div>
    );
}

export default CoursesComponent;
