import React, { useEffect, useState } from "react";
import CourseComponent from "./CoursesComponent";
import { coursesOperations } from "app/store/state/courses";
import { useDispatch, useSelector } from "react-redux";
import { bitsOperations } from "app/store/state/bits";

function Courses(props) {
  const dispatch = useDispatch();
  const [activeSection, setActiveSection] = useState("courses");
  const courses = useSelector((state) => state.courses);
  const bits = useSelector((state) => state.bits);

  const openCourseDetails = (courseId) => {
    props.history.push(`/course/${courseId}`);
  };

  const openBitDetails = (bitId) => {
    props.history.push(`/bit/${bitId}`);
  };

  const [toAddCourse, redirectToAddCourse] = useState(false);
  const [toAddBit, redirectToAddBit] = useState(false);

  useEffect(() => {
    dispatch(coursesOperations.loadCourses());
    dispatch(bitsOperations.loadBits());
  }, [dispatch]);

  const goToAddCourse = () => {
    redirectToAddCourse(true);
  };

  const goToAddBit = () => {
    redirectToAddBit(true);
  };

  const switchView = () => {
    if (activeSection === "courses") setActiveSection("bits");
    if (activeSection === "bits") setActiveSection("courses");
  };

  return (
    <CourseComponent
      courses={courses}
      bits={bits}
      toAddCourse={toAddCourse}
      toAddBit={toAddBit}
      activeSection={activeSection}
      switchView={switchView}
      openCourseDetails={openCourseDetails}
      openBitDetails={openBitDetails}
      goToAddCourse={goToAddCourse}
      goToAddBit={goToAddBit}
    />
  );
}

export default Courses;
