import React from "react";
import PropTypes from "prop-types";
import Card from "react-bootstrap/Card";
import Badge from "react-bootstrap/Badge";
import Button from "react-bootstrap/Button";
import { LEVELS_MAP } from "app/constants/levels";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faGraduationCap,
  faAngleRight,
  faPencilAlt,
  faTrash,
  faPlus,
} from "@fortawesome/free-solid-svg-icons";

import "./course-card.scss";

function CourseCard({
  id,
  name,
  featuredImage,
  price,
  freeFlag,
  overview,
  isPublished,
  openCourseDetails,
  mode = "list",
  toggleEditMode,
  toggleDeleteMode,
  openLessons,
  openModules,
  level,
  type,
}) {
  const openCourse = (e) => {
    e.stopPropagation();
    openCourseDetails(id);
  };
  return (
    <Card body className={`course-card ${mode === "header" ? "bg-light" : ""}`}>
      <div className="course-wrapper p-3">
        <div className="course-image">
          {featuredImage ? (
            <img src={featuredImage} alt="Logo" />
          ) : (
            <FontAwesomeIcon icon={faGraduationCap} size="2x" />
          )}
        </div>
        <div className="course-info ml-3">
          <div
            className={`course-title ${
              mode === "header" ? "h5" : "h6"
            } clamp-1 ellipsis font-weight-bold mb-0`}
          >
            {name}
          </div>
          {mode === "list" && (
            <small className="course-overview text-muted ellipsis clamp-3">
              {overview}
            </small>
          )}
          {mode === "list" && (
            <div className="course-tags mt-1">
              {freeFlag ? (
                <Badge pill variant="success" className="mr-1">
                  Free
                </Badge>
              ) : (
                <Badge pill variant="info" className="mr-1">
                  Paid
                </Badge>
              )}
              {isPublished ? (
                <Badge pill variant="success" className="mr-1">
                  Published
                </Badge>
              ) : (
                <Badge pill variant="warning" className="mr-1">
                  Not Published
                </Badge>
              )}
              {level && (
                <Badge pill variant="primary" className="mr-1">
                  {LEVELS_MAP[level]}
                </Badge>
              )}
              {type && (
                <Badge pill variant="secondary" className="mr-1">
                  {type}
                </Badge>
              )}
            </div>
          )}
        </div>
        {!freeFlag && mode === "list" && (
          <h6 className={`text-info fw-bold mr-4 ml-5 mb-0 font-weight-bold`}>
            <small>INR</small> {price}
          </h6>
        )}

        <div className="action-panel">
          {mode === "list" && (
            <div className="action-button pointer" onClick={openCourse}>
              <FontAwesomeIcon icon={faAngleRight} />
            </div>
          )}
          {mode === "header" && (
            <>
              <Button variant="primary" className="ml-2" onClick={openModules}>
                <FontAwesomeIcon icon={faPlus} className="mr-2" />
                Add Module
              </Button>
              <Button variant="success" className="ml-2" onClick={openLessons}>
                <FontAwesomeIcon icon={faPlus} className="mr-2" />
                Add Lesson
              </Button>
              <Button
                variant="warning"
                className="ml-2"
                onClick={toggleEditMode}
              >
                <FontAwesomeIcon icon={faPencilAlt} className="mr-2" />
                Edit
              </Button>
              <Button
                variant="danger"
                className="ml-2"
                onClick={toggleDeleteMode}
              >
                <FontAwesomeIcon icon={faTrash} className="mr-2" />
                Delete
              </Button>
            </>
          )}
        </div>
      </div>
    </Card>
  );
}

CourseCard.propTypes = {
  name: PropTypes.string,
  featuredImage: PropTypes.string,
  slug: PropTypes.string,
  price: PropTypes.any,
  freeFlag: PropTypes.bool,
  overview: PropTypes.string,
  isPublished: PropTypes.bool,
  openCourseDetails: PropTypes.func,
};

export default CourseCard;
